<template>
 <div class="ipage">
  <div class="interfere-page" >
    <!-- æ­£å¿µå¼•å¯¼éƒ¨åˆ† -->
    <div class="mindfulness-section">
      <h2>æ­£å¿µç»ƒä¹ </h2>
      <div class="audio-control">
        <div v-for="audio in audioGuides" :key="audio.id" class="audio-item">
          <button @click="toggleAudio(audio.id)" :class="{ 'playing': selectedAudio === audio.id }">
            {{ audio.name }}
          </button>
          <audio :id="audio.id" :src="audio.src"></audio>
        </div>
      </div>
    </div>

    <!-- æ—¥è®°è®°å½•éƒ¨åˆ† -->
    <div class="journal-section">
      <h2>ä»Šæ—¥æ—¥è®°</h2>
      <textarea
        v-model="journalEntry"
        placeholder="è®°å½•ä½ çš„æƒ³æ³•å’Œæ„Ÿå—..."
        rows="10"
      ></textarea>
      <div class="mood-selector">
        <span>ä»Šæ—¥å¿ƒæƒ…ï¼š</span>
        <select style="background-color: #ffffff4a;" v-model="selectedMood">
          <option value="">é€‰æ‹©å¿ƒæƒ…</option>
          <option v-for="mood in moods" :key="mood" :value="mood">
            {{ mood }}
          </option>
        </select>
      </div>
      <button @click="saveJournalEntry" :disabled="!canSaveEntry">
        ä¿å­˜æ—¥è®°
      </button>
    </div>

    <!-- å†å²æ—¥è®°åˆ—è¡¨ -->
    <div class="journal-history" v-if="journalEntries.length > 0">
      <h2>å†å²æ—¥è®°</h2>
      <ul>
        <li v-for="(entry, index) in journalEntries" :key="entry.date">
          <div v-if="editingIndex !== index">
            <strong>{{ formatDate(entry.date) }}</strong> - 
            å¿ƒæƒ…: {{ entry.mood }}
            <p>{{ truncateText(entry.content) }}</p>
            <div class="entry-actions">
              <button @click="startEditing(index)" class="edit-btn">ç¼–è¾‘</button>
              <button @click="deleteEntry(index)" class="delete-btn">åˆ é™¤</button>
            </div>
          </div>
          <div v-else>
            <textarea v-model="editingEntry.content" rows="3"></textarea>
            <select v-model="editingEntry.mood">
              <option v-for="mood in moods" :key="mood" :value="mood">
                {{ mood }}
              </option>
            </select>
            <div class="entry-actions">
              <button @click="saveEdit(index)" class="save-btn">ä¿å­˜</button>
              <button @click="cancelEdit" class="cancel-btn">å–æ¶ˆ</button>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
 </div>
</template>

<script>
export default {
  name: 'InterferePage',
  data() {
    return {
      // éŸ³é¢‘ç›¸å…³
      selectedAudio: '',
      isAudioPlaying: false,
      audio: null,
      audioGuides: [
        { id: 'breathing', name: 'å‘¼å¸ç»ƒä¹ ', src: 'http://music.163.com/song/media/outer/url?id=2015227525.mp3'},
        { id: 'bodyscan', name: 'èº«ä½“æ‰«æ', src: 'http://music.163.com/song/media/outer/url?id=1912344439.mp3' },
        { id: 'loving-kindness', name: 'æ…ˆå¿ƒå†¥æƒ³', src: 'http://music.163.com/song/media/outer/url?id=1317065752.mp3' },
      ],

      // æ—¥è®°ç›¸å…³
      journalEntry: '',
      selectedMood: '',
      moods: ['å¼€å¿ƒ', 'å¹³é™', 'ç„¦è™‘', 'æ²®ä¸§', 'å…´å¥‹'],
      journalEntries: [],
      editingIndex: -1,
      editingEntry: { content: '', mood: '' },

    }
  },
  computed: {
    canSaveEntry() {
      return this.journalEntry.trim() !== '' && this.selectedMood !== ''
    }
  },
  methods: {
    // è®¡æ—¶å™¨ç›¸å…³æ–¹æ³•
    toggleTimer() {
      if (this.isTimerRunning) {
        this.pauseTimer()
      } else {
        this.startTimer()
      }
    },
    startTimer() {
      this.isTimerRunning = true
      this.timerInterval = setInterval(() => {
        if (this.remainingTime > 0) {
          this.remainingTime--
        } else {
          this.pauseTimer()
        }
      }, 1000)
    },
    pauseTimer() {
      this.isTimerRunning = false
      clearInterval(this.timerInterval)
    },
    resetTimer() {
      this.pauseTimer()
      this.remainingTime = this.initialTime
    },
    formatTime(seconds) {
      const mins = Math.floor(seconds / 60)
      const secs = seconds % 60
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    },

    // éŸ³é¢‘ç›¸å…³æ–¹æ³•
    toggleAudio(audioId) {
      const audioElement = document.getElementById(audioId);
      if (this.selectedAudio === audioId && !audioElement.paused) {
        this.stopAudio();
      } else {
        this.stopAudio(); // åœæ­¢å½“å‰æ’­æ”¾çš„éŸ³é¢‘ï¼ˆå¦‚æœæœ‰ï¼‰
        this.selectedAudio = audioId;
        this.playAudio(audioElement);
      }
    },
    playAudio(audioElement) {
      audioElement.play();
      this.isAudioPlaying = true;
    },
    stopAudio() {
      if (this.selectedAudio) {
        const audioElement = document.getElementById(this.selectedAudio);
        audioElement.pause();
        audioElement.currentTime = 0;
      }
      this.isAudioPlaying = false;
      this.selectedAudio = '';
    },

    // æ—¥è®°ç›¸å…³æ–¹æ³•
    saveJournalEntry() {
      const entry = {
        date: new Date().toISOString(),
        content: this.journalEntry,
        mood: this.selectedMood
      }
      this.journalEntries.unshift(entry)
      this.saveToLocalStorage()
      this.journalEntry = ''
      this.selectedMood = ''
    },
    formatDate(dateString) {
      return new Date(dateString).toLocaleDateString()
    },
    truncateText(text, maxLength = 50) {
      return text.length > maxLength ? text.slice(0, maxLength) + '...' : text
    },
    startEditing(index) {
      this.editingIndex = index
      this.editingEntry = { ...this.journalEntries[index] }
    },
    saveEdit(index) {
      this.journalEntries[index] = { ...this.editingEntry }
      this.cancelEdit()
      this.saveToLocalStorage()
    },
    cancelEdit() {
      this.editingIndex = -1
      this.editingEntry = { content: '', mood: '' }
    },
    deleteEntry(index) {
      if (confirm('ç¡®å®šè¦åˆ é™¤è¿™æ¡æ—¥è®°å—ï¼Ÿ')) {
        this.journalEntries.splice(index, 1)
        this.saveToLocalStorage()
      }
    },

    // æœ¬åœ°å­˜å‚¨
    saveToLocalStorage() {
      localStorage.setItem('journalEntries', JSON.stringify(this.journalEntries))
    },
    loadFromLocalStorage() {
      const savedEntries = localStorage.getItem('journalEntries')
      if (savedEntries) {
        this.journalEntries = JSON.parse(savedEntries)
      }
    },

  },
  
  beforeDestroy() {
    // æ¸…ç†å®šæ—¶å™¨å’ŒéŸ³é¢‘
    this.pauseTimer()
    this.stopAudio()
  }
}
</script>

